.payment {
    overflow: hidden;
    padding: 0;
}

.payment__items-container {
    display: flex;
    flex-wrap: wrap;
    column-gap: 10px;
    row-gap: 30px;
}

.payment__item-no-items {
    height: 150px;
    display: flex;
    align-items: center;
}

.payment__item-no-items strong {
    font-size: 20px;
}


.cart-item img {
    width: 25% !important;
}

.payment__subtotal {
    margin: 20px 0;
}

.payment .err-alert {
    margin: 15px 40px;
}
@media only screen and (max-width: 768px) {
    .payment__items-container {
        justify-content: center;
    }
}